.games-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    >div {
        display: grid;
        grid-gap: 20px 46px; 
        max-width: 100%;
        grid-template-columns: repeat(6, 122px); 
        &:nth-child(odd)>a:last-child {
            grid-column: 5 / 7;
        }
        &:nth-child(even)>a:first-child {
            grid-column: 1 / 3;
        }
        &:nth-child(odd)>a:last-child, &:nth-child(even)>a:first-child {
            grid-row: 1 / 3;                                   /* 5 */
           
            max-width: 288px;   
            >img {
                height: 406px;
            }
            &:hover::after {
top: 171px;
width: 64px;
height: 64px;

background-image: url('../../public/images/play.svg');;
background-size:contain;
            }
        }
        >a {
            position: relative;
            max-width: 122px;
            width: 100%;
            display: flex;
            align-items: center;
            flex-direction: column;
            &:hover::after {
                position: absolute;
                top: 65px;
                
                
                width: 38px;
                height: 38px;
                content: '';
                background-image: url('../../public/images/play.svg');;
            }
            &:hover>img {
                opacity: 0.5;
            }
            >img {
                max-width: 100%;
                border-radius: 10px;
                height: 172px;
                object-fit: cover;
                margin-bottom: 4px;
            }
            >h4 {
                text-overflow: ellipsis;
                max-width: 100%;
                white-space: nowrap;
                overflow: hidden;
                font-weight: 400;
                font-size: 15px;
                line-height: 20px;
                align-self: flex-start;
            }
            >span {
                opacity: 0.4;
                font-size: 12px;
                align-self: flex-start;
                line-height: 18px;
                
            }
        }
    }
