    display: none;
}
span {
width: 100%;
transition: 0.3s;
height: 22px;
box-sizing: border-box;
position: relative;
border: 2px solid var(--purpur);
border-radius: 6px;
&::after {
    content: '';
    position: absolute;
top: 3px;
    width: 13px;
    height: 12px;
