.games-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.games-list > div {
  display: grid;
  grid-gap: 20px 46px;
  max-width: 100%;
  grid-template-columns: repeat(6, 122px);
}
.games-list > div:nth-child(odd) > a:last-child {
  grid-column: 5/7;
}
.games-list > div:nth-child(even) > a:first-child {
  grid-column: 1/3;
}
.games-list > div:nth-child(odd) > a:last-child, .games-list > div:nth-child(even) > a:first-child {
  grid-row: 1/3; /* 5 */
  max-width: 288px;
}
.games-list > div:nth-child(odd) > a:last-child > img, .games-list > div:nth-child(even) > a:first-child > img {
  height: 406px;
}
.games-list > div:nth-child(odd) > a:last-child:hover::after, .games-list > div:nth-child(even) > a:first-child:hover::after {
  top: 171px;
  width: 64px;
  height: 64px;
  background-image: url("../../public/images/play.svg");
  background-size: contain;
}
.games-list > div > a {
  position: relative;
  max-width: 122px;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.games-list > div > a:hover::after {
  position: absolute;
  top: 65px;
