.toggle-switch {
  width: 41px;
  display: flex;
  position: relative;
}
.toggle-switch input {
  display: none;
}
.toggle-switch span {
  width: 100%;
  transition: 0.3s;
  height: 22px;
  box-sizing: border-box;
  position: relative;
  border: 2px solid var(--purpur);
  border-radius: 6px;
}
.toggle-switch span::after {
  content: "";
  position: absolute;
  top: 3px;
  width: 13px;
  height: 12px;
  transition: 0.2s;
  left: 3px;
  border-radius: 3px;
  background: var(--purpur);
}
.toggle-switch input:checked + span {
  background: var(--purpur);
  border: 2px solid #4D35AF;
}
.toggle-switch input:checked + span::after {
  left: 21px;
  background: white;
}

